import React, {useState} from 'react';
import "./PMSoftwareQuizStyles.css"
import QuizQuestion from "../QuizQuestion/QuizQuestion";
import jsonQuestions from "./PMSoftwareQuizQuestions.json";
import QuizResult from "../QuizResult/QuizResult";

function PmSoftwareQuiz(props) {
    let jsonQuestions = require("./PMSoftwareQuizQuestions.json");

    const [activeQuestionNumber, setActionQuestionNumber] = useState(0);
    const [selectedAnswers, setSelectedAnswers] = useState([])

    return (
        <div className="pmSoftwareQuizWrapper">
            <img src="https://global-uploads.webflow.com/5f073e32d304276cc8b4ff30/5fbc3a52ef4b4f37559b9b45_Blue%20Logo%20-%20Left%20Emblem.svg" className="quiz-result-header-image"/>
            {activeQuestionNumber < jsonQuestions.questions.length ?
                <QuizQuestion question={jsonQuestions.questions[activeQuestionNumber]} activeQuestionNumber={activeQuestionNumber} setActionQuestionNumber={setActionQuestionNumber} selectedAnswers={selectedAnswers} setSelectedAnswers={setSelectedAnswers}/> :
                // <div>{jsonQuestions["result" + selectedAnswers[0]][Math.floor(Math.random() * 3)]["mainTitle"]}</div>
                <QuizResult result={jsonQuestions["result" + selectedAnswers[0]][Math.floor(Math.random() * 3)]}/>
            }
        </div>
    );
}

export default PmSoftwareQuiz;