import React from 'react';
import "./ContinueButtonStyles.css"

function ContinueButton({setActionQuestionNumber, activeQuestionNumber, activeAnswer, setActiveAnswer, question, selectedAnswers, setSelectedAnswers}) {

    const handleClick = () => {

        if(activeAnswer){
            let temp = activeQuestionNumber
            let tempArray = [...selectedAnswers]

            tempArray.push(activeAnswer.split("questionAnswer")[1])

            if(question.conditional.isConditional){
                if(activeAnswer === question.conditional.correctAnswer){
                    temp++
                }else{
                    temp = temp + question.conditional.incorrectDisplacement
                }
            }else {
                temp++
            }



            setSelectedAnswers(tempArray)
            setActionQuestionNumber(temp)
            setActiveAnswer("")


        }
    }

    return (
        <div className="continue-button-wrapper">
            <button
                onClick={handleClick}
                className="continue-button"
                id="continue-button-id"
            >
                <span className="continue-button-text">Continue</span>
            </button>
        </div>

    );
}

export default ContinueButton;